* {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  margin: 0;
  padding: 0;
}

.game { background: linear-gradient(to left, #00ccff, #ff99cc);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f2f2f2;
}

.game-board {
  background: linear-gradient(to left, #ff99cc, #00ccff);
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  border-radius: 6px;
  padding: 24px;
}

.status {
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 12px;
}

.board-row {
  display: flex;
}

.square {
  width: 64px;
  height: 64px;
  font-size: 32px;
  font-weight: bold;
  text-align: center;
  line-height: 64px;
  border: none;
  background: #fff;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  margin-right: -1px;
  margin-bottom: -1px;
  cursor: pointer;
}

.square:hover {
  background-color: #f2f2f2;
}

.new { margin-left: 0px;
  margin: 1px;
  background-color: #ff99cc;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 10px;
  font-size: 14px;
  cursor: pointer;
}

.winningSquare {
  background: #00ccff;
}

.winningSquare:hover {
  background-color: #00ccff;
}


